.container{max-width:1024px;margin:0 auto}.row{display:flex;margin-left:-5px;margin-right:-5px;margin-bottom:10px}.row.wrap{flex-wrap:wrap}.row.v_center{align-items:center}.row.v_start{align-items:flex-start}.row.v_end{align-items:flex-end}.row.h_start{justify-content:flex-start}.row.h_center{justify-content:center}.row.h_end{justify-content:flex-end}.row.h_between{justify-content:space-between}.row.h_around{justify-content:space-around}.col{margin-left:5px;margin-right:5px;flex-shrink:0}.col_grow{flex-grow:1}.col_1{margin-left:5px;margin-right:5px;flex-basis:calc(100% / 12 - 10px)}.col_2{margin-left:5px;margin-right:5px;flex-basis:calc(200% / 12 - 10px)}.col_3{margin-left:5px;margin-right:5px;flex-basis:calc(300% / 12 - 10px)}.col_4{margin-left:5px;margin-right:5px;flex-basis:calc(400% / 12 - 10px)}.col_5{margin-left:5px;margin-right:5px;flex-basis:calc(500% / 12 - 10px)}.col_6{margin-left:5px;margin-right:5px;flex-basis:calc(600% / 12 - 10px)}.col_7{margin-left:5px;margin-right:5px;flex-basis:calc(700% / 12 - 10px)}.col_8{margin-left:5px;margin-right:5px;flex-basis:calc(800% / 12 - 10px)}.col_9{margin-left:5px;margin-right:5px;flex-basis:calc(900% / 12 - 10px)}.col_10{margin-left:5px;margin-right:5px;flex-basis:calc(1000% / 12 - 10px)}.col_11{margin-left:5px;margin-right:5px;flex-basis:calc(1100% / 12 - 10px)}.col_12{margin-left:5px;margin-right:5px;flex-basis:calc(1200% / 12 - 10px)}.col_v_start{align-self:flex-start}.col_v_center{align-self:center}.col_v_end{align-self:flex-end}.col_h_end{display:flex;justify-content:flex-end}.col_h_center{display:flex;justify-content:center}.col_h_start{display:flex;justify-content:flex-start}.divider{flex-grow:1;flex-shrink:1}
