.checkbox{position:relative;display:inline-flex;user-select:none;cursor:pointer}[theme="light"] .checkbox.hover:hover{background:#DDD}[theme="dark"] .checkbox.hover:hover{background:#777}.label{cursor:pointer;user-select:none;line-height:24px;padding-left:44px;padding-right:10px;padding-top:10px;padding-bottom:10px;z-index:1}.box{position:absolute;flex:0 0 24px;width:24px;height:24px;left:10px;top:10px;border-radius:0}[theme="light"] .box{border:#000 2px solid}[theme="dark"] .box{border:#fff 2px solid}.box .check{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);font-size:calc($checkbox-size);font-weight:700}[theme="light"] .box .check{color:#000}[theme="dark"] .box .check{color:#fff}.box.center{top:50%;transform:translateY(-50%)}.native{position:absolute;z-index:-1;opacity:0}
