.icons{position:relative;padding-top:60px;max-height:100%;height:100%;overflow:hidden}.search{position:absolute;left:0;right:0;top:0;display:flex;justify-content:center}.row{display:flex;flex-wrap:wrap;max-height:100%;overflow:scroll}.col{flex:0 0 20%;max-width:20%;display:flex;flex-direction:column;justify-content:flex-start;align-items:center;margin-bottom:70px;padding:10px;position:relative}.icon{font-size:32px}.text{white-space:nowrap;overflow:hidden;outline:none;text-align:center;user-select:all;border:none;position:absolute;top:calc(100% + 20px)}.text:focus{overflow:visible;width:1000px;z-index:1000}.text:not(:focus){max-width:100%}.text.name{top:100%}.text.code{top:calc(100% + 20px)}
